<template>
  <div class="message-page">
    <header class="title-box">
      <div class="title">留言板</div>
      <div class="write">
        <img src="@/assets/image/write.png" class="write-icon" alt />写留言
      </div>
    </header>
    <ul class="message-box">
      <li class="message" v-for="(item,index) in messageList" :key="index">
        <div class="username ellipsis">{{ item.username }}</div>
        <div class="user-message">{{ item.userMessage }}</div>
        <div class="flower-box">
          <div class="time">{{ item.time }}</div>
          <div
            class="flower"
            :style="{backgroundImage: back[item.isFlower]}"
            @click="flower(index)"
          ></div>
          <div class="number">{{ item.number }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      messageList: [],
      back: [
        "url(" + require("../assets/image/flower-n.png") + ")",
        "url(" + require("../assets/image/flower-y.png") + ")"
      ]
    };
  },
  mounted() {
    const list = [
      {
        username: "张国荣",
        userMessage:
          "张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣张国荣",
        time: "3-19 23:10",
        isFlower: 1,
        number: "1314"
      },
      {
        username: "Leslie",
        userMessage:
          "LeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslieLeslie",
        time: "3-19 23:10",
        isFlower: 0,
        number: "520"
      }
    ];
    for (let i = 0; i < 10; i++) {
      this.messageList.push(
        JSON.parse(JSON.stringify(list[0])),
        JSON.parse(JSON.stringify(list[1]))
      );
    }
  },
  methods: {
    flower(index) {
      if (this.messageList[index].isFlower) {
        this.messageList[index].isFlower = 0;
        this.messageList[index].number--;
      } else {
        this.messageList[index].isFlower = 1;
        this.messageList[index].number++;
      }
    }
  }
};
</script>

<style lang="scss">
.message-page {
  height: fill-available;
  background-color: #fff;
  & > .title-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    padding: 0 0.3rem;
    border-bottom: 0.01rem solid #ededed;
    box-sizing: border-box;
    @include flex(space-between, center, row, nowrap);
    background-color: #fff;
    .title {
      @include font-style(0.4rem, #333, 600);
    }
    .write {
      width: 1.8rem;
      height: 0.6rem;
      background-color: #eb7350;
      border-radius: 0.4rem;
      @include font-style(0.28rem, #fff, 400);
      @include flex(flex-start, center, row, nowrap);
      .write-icon {
        width: 0.4rem;
        height: 0.4rem;
        margin-left: 0.2rem;
        margin-right: 0.15rem;
      }
    }
  }
  & > .message-box {
    margin-top: 1rem;
    width: 100%;
    box-sizing: border-box;
    padding: 0 0.3rem;
    .message {
      width: 100%;
      padding: 0.3rem 0;
      border-bottom: 0.01rem solid #ededed;
      box-sizing: border-box;
      @include flex(flex-start, flex-start, column, nowrap);
      .username {
        width: 100%;
        line-height: 0.4rem;
        @include font-style(0.28rem, #666, 400);
      }
      .user-message {
        margin-top: 0.1rem;
        line-height: 0.4rem;
        white-space: wrap;
        word-break: break-all;
        @include font-style(0.28rem, #333, 400);
      }
      .flower-box {
        width: 100%;
        margin-top: 0.2rem;
        @include flex(flex-start, center, row, nowrap);
        .time {
          flex: 1;
          @include font-style(0.24rem, #999, 400);
        }
        .flower {
          width: 0.4rem;
          height: 0.4rem;
          margin-right: 0.1rem;
          background-size: 100% 100%;
          background-color: transparent;
        }
        .number {
          @include font-style(0.24rem, #808080, 400);
        }
      }
      &:last-child {
        border: none;
      }
    }
  }
}
</style>